.navigation-back {
  flex-direction: row;
  justify-content: start;
  align-items: center;
  display: flex;
}

.back-arrow {
  padding-left: 1rem;
}

.hire-form-section {
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-evenly;
  align-items: center;
  display: flex;
}

.primary-form-container {
  padding: 1rem 0;
}

.secondary-form-container {
  margin: 0 auto;
}

.form-heading {
  text-align: center;
  margin-bottom: 2rem;
}

.form-heading h3 {
  margin-bottom: 1rem;
  font-size: 1.5rem;
  font-weight: 400;
}

.form {
  gap: 1rem;
  width: 100%;
  max-width: 640px;
  margin: 0 auto;
  display: grid;
}

.input-label {
  margin-bottom: .5rem;
  font-size: medium;
  display: inline-block;
}

.input {
  color: #fff;
  background-color: #0000;
  border: 1px solid #fff3;
  border-radius: 8px;
  outline: none;
  width: 100%;
  padding: .5rem .75rem;
  transition: all .3s ease-in-out;
}

.input:focus {
  background-color: #0003;
  outline: none;
}

.full-width-input-field {
  grid-column: span 2 / span 2;
}

.text-area {
  height: 16rem;
}

.submit-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.submit-button {
  color: #fff;
  text-align: center;
  background-color: #0369a1;
  border-radius: 8px;
  padding: .5rem 2rem;
  transition: all .3s ease-in-out;
  display: inline-block;
}

.submit-button:hover {
  background-color: #0369a1cc;
}

@media screen and (width >= 768px) {
  .secondary-form-container {
    padding: 0 2rem;
  }

  .form-heading {
    margin-bottom: 4rem;
  }

  .form-heading h3 {
    margin-bottom: 1.5rem;
  }

  .form {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media screen and (width >= 1024px) {
  .primary-form-container {
    padding: 3rem 0;
  }

  .form-heading h3 {
    font-size: 2rem;
  }
}

@media screen and (width <= 768px) {
  .back-arrow {
    padding-left: 0;
  }
}

